body, html {
  font-family: 'Nunito', sans-serif;
  margin: 0;
  padding: 0;
}

h1, h2, h3 {
  font-family: "Open Sans", sans-serif;
  font-size: 1.8rem;
  margin-bottom: .5rem;
  font-weight: 500;
  line-height: 1.2;
  color: inherit;
}

li {
  list-style: none;
}

ul {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

figure {
  text-align: center;
}

figcaption {
  line-height: initial;
  font-weight: initial;
  color: initial;
}

.App {
  text-align: center;
}

.bldr {
  font-weight: bolder;
  font-size: larger;
}

.nodisplay {
  display: none!important;
}

.content {
  position: relative;
  background-color: white;
}

.project-tiles {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

}

.project-tile {
  width: 300px;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
  cursor: pointer;
}

.projects {
  margin: 0 20%;
}

.project-skills {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.project-skills div {
  display: inline-block;
  padding: 10px;
  font-size: 5em;
  font-weight: 500;
}

.project-tile:hover {
  background-blend-mode: soft-light;
  background-color: #00000087;
}

.project-tile:hover .text-reveal {
  visibility: visible!important;
}

/*.header {
  height: 900px;
  background-image: linear-gradient(#aee0fe,white);
}*/

.header {
  display: flex;
  justify-content: space-around;
  padding: 20px;
  font-size: 2em;
}


.header li {
  display: inline-block;
  margin-right:20px;
}

.parallax-layer {
  position: fixed;
  background-position: center;
  transform: translateY(0px);
  height: 400px;
  width: 100%;
  background-repeat: repeat-x;
}

.dm-title {
  display: inline-block;
  font-size: 4rem;
  font-weight: 800;
  letter-spacing: 1rem;
  color:white;
  font-family: 'Bungee Outline', cursive;
  visibility: hidden;
}

.dm-anime {
  animation-name: dm-anime;
  animation-duration: 3s;
  visibility: visible!important;
  margin-top: 15rem;
}

@keyframes dm-anime {
  0% {
    margin-top: 30rem;
  }
  100% {
    margin-top: 15rem;
  }
}

