.layer1 {
  background-image: url('./assets/PurpleBuildings-01.svg');
  top:400px;
}

.layer2 {
  background-image: url('./assets/PinkBuildings-01.svg');
  top:350px;
}

.layer3 {
  background-image: url('./assets/YellowBuildings-01.svg');
  top:300px;
}

.ofHome-tile {
  background-image: url('./assets/OfHome_Thumb.png');
  background-size: cover;
  background-repeat: no-repeat;
}

.selectim-tile {
  background-image: url('./assets/Header_Selectim.png');
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
}

.thisSite-tile {
  background-image: url('./assets/ga_inverse.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.valerie-tile {
  background-image: url('./assets/valerieSite.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.medscape-tile {
  background-image: url('./assets/mscp-logo.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

#vue-logo, #react-logo {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  padding: 0;
  width: 70px;
  height: 70px;
}
#vue-logo {
  background-image: url('./assets/vue-logo.svg');
  margin-left: 10px;
}
#react-logo {
  background-image: url('./assets/React-icon.svg');
  height: 105px;
  width: 105px;
}

.text-reveal {
  visibility: hidden;
  color: white;
  padding: 20px;
  font-size: 1.5rem;
}

.project-window {
  margin: 0 auto;
  max-width: 850px;
  text-align: left;
  padding: 50px 0;
}

.title {
  font-size: 2.5em;
  /* margin-bottom: 50px; */
  text-align: center;
  letter-spacing: .05em;
}

.project-text, .blurb {
  line-height: 2em;
  /*background-color: #5f55c3;*/
  padding: 15px;
  color: grey;
  font-weight: 300;
  max-width: 850px;
  margin: 0 auto;
  /*border-radius: 15px;*/
}

.back-arrow {
  margin-bottom: 50px;
}

.back-arrow i {
  font-size: 3em;
  cursor: pointer;
}

.back-arrow i:hover {
  color: #5f55c3;
}

.skills {
  margin: 50px 0;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.skill {
  border: 3px solid #E9ECEF;
  border-radius: 15px;
  padding: 10px;
  min-width: 350px;
  text-align: center;
  min-height: 150px;
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skill-title {
  font-weight: bolder;
  margin-bottom: .75rem;
}

.skill ul {
  margin:0;
  padding: 0;
  text-align: left;
  display: inline-block;
}

.skill-icon {
  border-radius: 50%;
  width: 90px;
  height: 90px;
  background-color: #dce7eb;
  font-size: 36px;
  color: #1da1f2;
  padding: .25rem;
  margin-top: 1.5rem;
  margin-bottom: 1rem;
}

.skill-icon i {
  position: relative;
  top: 23%;
}

.skill li {
  padding: .50rem 1.25rem;
  font-size: 90%;
}

.skill li>i {
  margin-right: 5px;
  color: #1DA1F2;
  font-size: 85%;
}

.project-img {
  padding: 50px 0;
  text-align: center;
}

.project-img img {
  max-width: 800px;
}

.footer div:first-of-type {
  background-color: #FFCA3A;
  color: #000;
  padding: 3rem 2rem;
}

.footer i {
  margin-right: 5px;
  font-size: larger;
}

.footer p {
  line-height: 2.5em;
}

.copy {
  padding: 20px 0;
  font-size: large;
}

.btn {
  display: inline-block;
  background-color: #000;
  color: #fff;
  border-radius: 50rem;
  font-weight: 600;
  padding: 1rem 1.5rem;
  cursor: pointer;
}

.btn:not(#resume-btn):hover {
  background-color: #23272b!important;
}

.hover-white:hover{
  color:white!important;
}

.extra {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px 0;
}

.extra-title {
  font-size: x-large;
  padding: 50px;
  text-align: center;
}

.extra img {
  max-height: 450px;
}

.links .btn {
  margin: 0 1rem 1rem 0;
}

.links {
  text-align: center;
}

.project-links {
  text-align: center;
}

.project-links li {
  margin-right: 50px;
  padding: 15px 0;
  font-weight: 600;
  color: grey;
  font-size: 1.2rem;
}

.project-links li:hover {
  color:black;
}

li.selected {
  color: black;
}

.about-me-title {
  text-align: center;
  font-size: 2em;
  font-weight: 600;
  margin-top:25px;
}

.about-me-text {
  text-align: left;
  color: grey;
  font-size: 1.3rem;
}

.about-me a {
  color: blue;
}

.selfie {
  display: inline-block;
  width: 300px;
  height:300px;
  background-image: url('./assets/selfie.JPG');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

#resume-btn {
  background-color: #ffca39;
  color: black;
}

#resume-btn:hover {
  background-color:#ffca397a;
}

@media only screen and (max-width: 600px) {
  .project-img img {
    max-width: 85%;
  }

  .extra img {
    max-width: 85%;
  }
  .header {
    flex-direction: column;
  }
  .project-links {
    text-align: left;
    padding: 0 15px;
  }
  .skill {
    min-width: 90%;
  }
}

@media only screen  and (min-width : 1224px) {
  .project-tile {
    width:25vw;
    height: 25vh;
  }

  .project-links li {
    display: inline-block;
  }
}

